// this file map the component props to ant-design-vue form element
// every prop should have five props
// 1 component  component
// 2  value  
// 3 intialTransform 
// 4 afterTransform  e 
// 5 text 
// 6   value checkbox  checked
import { h, VNode } from 'vue'
interface PropDetailType {
  component: string;
  eventName: string;
  intialTransform: (v: any) => any;
  afterTransform: (v: any) => any;
  text?: string;
  valueProp: string;
  subComponent?: string;
  options?: { text: string | VNode; value: any }[];
  extraProps?: { [key: string]: any };
  // 
  parent?: string;
  // 
  extraEvent?: string;
}
interface MapTypes {
  [key: string]: PropDetailType;
}

const defaultMap = {
  component: 'a-input',
  eventName: 'change',
  valueProp: 'value',
  intialTransform: (v: any) => v,
  afterTransform: (e: any) => e
}
const numberToPxHandle = {
  ...defaultMap,
  component: 'a-input-number',
  intialTransform: (v: string) => v ? parseInt(v) : 0,
  afterTransform: (e: number) => e ? `${e}px` : '0'
}
const fontFamilyArr = [
  { text: '', value: '"SimSun","STSong"' },
  { text: '', value: '"SimHei","STHeiti"' },
  { text: '', value: '"KaiTi","STKaiti"' },
  { text: '', value: '"FangSong","STFangsong"' },
  { text: 'Arial', value: '"Arial", sans-serif' },
  { text: 'Arial Black', value: '"Arial Black", sans-serif' },
  { text: 'Comic Sans MS', value: '"Comic Sans MS"' },
  { text: 'Courier New', value: '"Courier New", monospace' },
  { text: 'Georgia', value: '"Georgia", serif' },
  { text: 'Times New Roman', value: '"Times New Roman", serif' }
]

const fontFamilyOptions = fontFamilyArr.map(font => {
  return {
    value: font.value,
    text: h('span', { style: { fontFamily: font.value } }, font.text)
  }
})
const mapPropsToComponents: MapTypes = {
  text: {
    ...defaultMap,
    component: 'textarea-fix',
    afterTransform: (e: any) => e.target.value,
    text: '',
    extraProps: { rows: 3 }
  },
  href: {
    ...defaultMap,
    afterTransform: (e: any) => e.target.value,
    text: ''
  },
  fontSize: {
    ...numberToPxHandle,
    text: ''
  },
  fontFamily: {
    ...defaultMap,
    component: 'a-select',
    subComponent: 'a-select-option',
    text: '',
    options: [
      { value: '', text: '' },
      ...fontFamilyOptions
    ]
  },
  fontWeight: {
    ...defaultMap,
    component: 'icon-switch',
    intialTransform: (v: string) => v === 'bold',
    afterTransform: (e: boolean) => e ? 'bold' : 'normal',
    valueProp: 'checked',
    extraProps: { iconName: 'BoldOutlined', tip: '' }
  },
  fontStyle: {
    ...defaultMap,
    component: 'icon-switch',
    intialTransform: (v: string) => v === 'italic',
    afterTransform: (e: boolean) => e ? 'italic' : 'normal',
    valueProp: 'checked',
    extraProps: { iconName: 'ItalicOutlined', tip: '' }
  },
  textDecoration: {
    ...defaultMap,
    component: 'icon-switch',
    intialTransform: (v: string) => v === 'underline',
    afterTransform: (e: boolean) => e ? 'underline' : 'none',
    valueProp: 'checked',
    extraProps: { iconName: 'UnderlineOutlined', tip: '' }
  },
  lineHeight: {
    ...defaultMap,
    component: 'a-slider',
    text: '',
    intialTransform: (v: string) => v ? parseFloat(v) : 0,
    afterTransform: (e: number) => e.toString(),
    extraProps: { min: 0, max: 3, step: 0.1 }
  },
  textAlign: {
    ...defaultMap,
    component: 'a-radio-group',
    subComponent: 'a-radio-button',
    afterTransform: (e: any) => e.target.value,
    text: '',
    options: [
      { value: 'left', text: '' },
      { value: 'center', text: '' },
      { value: 'right', text: '' }
    ]
  },
  color: {
    ...defaultMap,
    component: 'color-picker',
    text: ''
  },
  backgroundColor: {
    ...defaultMap,
    component: 'color-picker',
    text: ''
  },
  // actions
  actionType: {
    ...defaultMap,
    component: 'a-select',
    subComponent: 'a-select-option',
    text: '',
    options: [
      { value: '', text: '' },
      { value: 'to', text: ' URL' }
    ]
  },
  url: {
    ...defaultMap,
    afterTransform: (e: any) => e.target.value,
    text: '',
    parent: 'actionType'
  },
  // sizes
  height: {
    ...defaultMap,
    component: 'a-input-number',
    intialTransform: (v: string) => v ? parseInt(v) : '',
    afterTransform: (e: number) => e ? `${e}px` : '',
    text: ''
  },
  width: {
    ...defaultMap,
    component: 'a-input-number',
    intialTransform: (v: string) => v ? parseInt(v) : '',
    afterTransform: (e: number) => e ? `${e}px` : '',
    text: ''
  },
  paddingLeft: {
    ...numberToPxHandle,
    text: ''
  },
  paddingRight: {
    ...numberToPxHandle,
    text: ''
  },
  paddingTop: {
    ...numberToPxHandle,
    text: ''
  },
  paddingBottom: {
    ...numberToPxHandle,
    text: ''
  },
  // border types
  borderStyle: {
    ...defaultMap,
    component: 'a-select',
    subComponent: 'a-select-option',
    text: '',
    options: [
      { value: 'none', text: '' },
      { value: 'solid', text: '' },
      { value: 'dashed', text: '' },
      { value: 'dotted', text: '' }
    ]
  },
  borderColor: {
    ...defaultMap,
    component: 'color-picker',
    text: ''
  },
  borderWidth: {
    ...defaultMap,
    component: 'a-slider',
    intialTransform: (v: string) => parseInt(v),
    afterTransform: (e: number) => e + 'px',
    text: '',
    extraProps: { min: 0, max: 20 }
  },
  borderRadius: {
    ...defaultMap,
    component: 'a-slider',
    intialTransform: (v: string) => parseInt(v),
    afterTransform: (e: number) => e + 'px',
    text: '',
    extraProps: { min: 0, max: 200 }
  },
  // shadow and opactiy
  opacity: {
    ...defaultMap,
    component: 'a-slider',
    text: '',
    intialTransform: (v: number) => v ? v * 100 : 100,
    afterTransform: (e: number) => (e / 100),
    extraProps: { min: 0, max: 100, reverse: true }
  },
  boxShadow: {
    ...defaultMap,
    component: 'shadow-picker'
  },
  position: {
    ...defaultMap,
    component: 'a-select',
    subComponent: 'a-select-option',
    text: '',
    options: [
      { value: '', text: '' },
      { value: 'absolute', text: '' }
    ]
  },
  left: {
    ...numberToPxHandle,
    text: 'X'
  },
  top: {
    ...numberToPxHandle,
    text: 'Y'
  },
  imageSrc: {
    ...defaultMap,
    component: 'image-processer'
  },
  backgroundImage: {
    ...defaultMap,
    component: 'background-processer',
    intialTransform: (v: string) => {
      if (v) {
        const matches = v.match(/\((.*?)\)/)
        if (matches && matches.length > 1) {
          return matches[1].replace(/('|")/g, '')
        } else {
          return ''
        }
      } else {
        return ''
      }
    },
    afterTransform: (e: string) => e ? `url('${e}')` : '',
    extraProps: { ratio: 8 / 15, showDelete: true },
    extraEvent: 'uploaded'
  },
  backgroundSize: {
    ...defaultMap,
    component: 'a-select',
    subComponent: 'a-select-option',
    text: '',
    options: [
      { value: 'contain', text: '' },
      { value: 'cover', text: '' },
      { value: '', text: '' }
    ]
  },
  backgroundRepeat: {
    ...defaultMap,
    component: 'a-select',
    subComponent: 'a-select-option',
    text: '',
    options: [
      { value: 'no-repeat', text: '' },
      { value: 'repeat-x', text: 'X' },
      { value: 'repeat-y', text: 'Y' },
      { value: 'repeat', text: '' }
    ]
  }
}

export default mapPropsToComponents
